body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.footer-container {
  display: flex;
  flex-direction: column;
  background-color: rgb(7, 33, 70);
  font-family: "BentonSansBBVA Medium", "Helvetica Neue", Arial, Helvetica,
    sans-serif;
  color: #eee;
}
.footer-primary {
    display: flex;
    flex-wrap: wrap;
    justify-content:space-around;
    align-items:center;
    font-size: 1rem;
    padding: 1.5rem 1rem;
  
}
.footer-top,
.footer-middle {
    cursor: pointer;
}
.footer-bottom {
    list-style: none;
}
.item {
    padding: .3rem;
}
.item > li {
    padding: .3rem;
}
.footer-secondary {
  background-color: rgb(0, 68, 129);
  height: 15%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  font-size: .899rem;
  /* font-weight: 900; */
  cursor: pointer;
  padding: .5em 0;
}
.footer-logo {
  padding: 1rem ;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 1.5rem;
}
.footer-logo >img {
  width: 100%;
  max-width: 100px;
  height:auto;
  padding-right: 2rem;
}
.footer-logo-text{

}
h3{
  color: #eee;
  font-size: 1.5rem;
}
p{
  color: #ccc;
  font-size: 1rem;
  /* margin-top:-1rem ; */
}
.header-drawer-container {
  font-family: "BentonSansBBVA Medium", "Helvetica Neue", Arial, Helvetica,sans-serif;
}
.web-drawer {
  overflow: hidden;
  position: sticky;
  z-index: 1;
  top: 8rem;
  background-color: rgb(0, 68, 129);
  height: 15%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1rem;
  cursor: pointer;
  /* border: 1px solid rgb(209, 36, 36); */
}
.web-drawer--item {
  /* color: red; */
  font-weight: 600;
  font-size: 1rem;
  padding: 1em 0;
  z-index: 1;
}
.web-drawer--item:hover {
  opacity: .3;
}
.list-item-text {
  padding: 0rem;
  margin: 0px;
  font-size: 1rem;
  color: #eee;
}
.list-item-text:hover{
  opacity: .3;
  
}
.header-dropdown {
    /* color:"#eee",fontWeight:550 ,fontSize:".905rem",
    fontFamily: "BentonSansBBVA Medium ,Helvetica Neue,Arial, Helvetica,sans-serif" */
    position: sticky;
    z-index: 1;
}
.header-dropdown > Button :hover{
    opacity: .3;
